
















































import { onShow } from "@dcloudio/uni-app";
import { useCool, useStore } from "/@/cool";

const { router, ui } = useCool();
const { user } = useStore();

// 刷新
async function refresh() {
	return user.get();
}

// 跳页面
function toPage(path: string) {
	router.push(path);
}

// 复制
function toCopy() {
	uni.setClipboardData({
		data: user.info?.num || "",
	});
}

// 开发中
function toDev() {
	ui.showToast("开发中");
}

onShow(() => {
	if (!user.token) {
		return router.login();
	}

	refresh();
});
